import '../index.scss'
export default {
    name: "rechargeUser",
    data() {
        return {
            price:'',
            dataInfo: {
                verify_status:0,
                user_info:{}
            },
            userInfo:{}
        }
    },
    components: {
    },
    onLoad(options) {
        this.getDataInfo()
    },
    methods: {
        handleInput(e){
            this.price = this.price.replace(/\D/g, '');
        },
        async recharge(){
            const self = this
            try{
                if(!this.price){return false}
                uni.showLoading({mask: true, title: '充值中...',})
                let info =  await this.$request({
                    url: this.$api.myrecharge.recharge,
                    method:'post',
                    data: {price:this.price}
                })
                let { code, data, msg } = info;
                if (code === 1) {
                    wx.requestPayment({
                        timeStamp: data.timeStamp,
                        nonceStr: data.nonceStr,
                        package: data.package,
                        signType: data.signType,
                        paySign: data.paySign,
                        success (res) { 
                            console.log('成功', res);
                            uni.showToast({title: '充值成功', icon: 'none'});
                            self.getDataInfo()
                            self.price = ''
                        },
                        fail (res) { 
                            console.log('失败', res);
                            uni.showToast({title: '充值失败', icon: 'none'});
                        },
                        complete(res){
                            console.log('complete', res);
                        }
                    })
                }
            }catch(e){
                uni.showToast({title: e, icon: 'none'});
            }
            // uni.hideLoading();;
        },
        goPath(url){
            uni.navigateTo({ url: url});
        },
        rePlay(){
            uni.navigateTo({ url: '/recharge/check_user/index?type=1' });
        },
        async cancelBind(){
            try{
                uni.showLoading({mask: true, title: '加载中...',})
                let info =  await this.$request({
                    url: this.$api.myrecharge.delBang,
                    data: {}
                })
                let { code, data, msg } = info;
                if (code === 1) {
                    uni.redirectTo({ url: '/pages/user-center/user-center' });
                } else {
                    uni.showToast({title: msg, icon: 'none'});
                }
                uni.showToast({title: msg, icon: 'none'});
            }catch(e){
                uni.showToast({title: e, icon: 'none'});
            }
            // uni.hideLoading();;

        },
        async getDataInfo (){
            uni.showLoading({mask: true, title: '加载中...',})
            try{
                let info =  await this.$request({
                    url: this.$api.myrecharge.bangStatus,
                    data: {}
                })
                let { code, data, msg } = info;
                if (code === 1) {
                    this.dataInfo = data
                    this.userInfo = data.user_info
                    console.log(this.dataInfo, data);
                } else {
                    // uni.showToast({title: msg, icon: 'none'});
                }
            }catch(e){}
            uni.hideLoading();;
        }
    }
}
